<template>
    <div id="doctor">
        <md-list class="custom-list md-triple-line">        
            <md-list-item v-for="item in Doctors">
                <router-link :to="{ name:'Doctor',params:{id:item.nid} }">
                <md-avatar>
                    <img :src="'https://api.bdhrbdf.com'+item.field_avatar" alt="">
                </md-avatar>

                <div class="md-list-text-container">
                    <span>{{item.title}}</span>
                    <span>{{item.field_position}}</span>
                    <p>{{ item.field_skill | substr(0,20) }}...</p>
                </div>

                <md-button class="md-icon-button md-list-action">
                    <md-icon class="md-primary">star</md-icon>
                </md-button>

                <md-divider class="md-inset"></md-divider>
                </router-link>
            </md-list-item>        
        </md-list>    
    </div>    
</template>

<script>
import Vue from 'vue' 
import axios from 'axios';

Vue.filter('substr',function(value,v1,v2){
    return value.substr(v1,v2);
  });
export default {
  name: 'DoctorList',
  data () {
    return {      
      Doctors: [],      
    }
  },
   
  methods: {
    fetchDate() {
      // 使用 axios获取数据          
      axios.get('https://api.bdhrbdf.com/api/'+this.$route.params.hospital+'/doctor')    
        .then(reponse=>{                
          this.Doctors=reponse.data;
        })
        .catch(error=>{
          alert('error');
        })
      }
    },
  created() {
    // 组件创建完后获取数据，
    // 此时 data 已经被 observed 了
    this.fetchDate();
  },
  watch: {
    // 如果路由有变化，会再次执行该方法
    "$route": "fetchDate"
  }  
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>    
    #doctor p{
        margin-top:5px;
        margin-bottom: 5px;
        padding-right:10px;
        color:#424242;
        font-size:15px;
    }
    .md-avatar {
        width: 70px;
        min-width: 70px;
        height: 70px;
        min-height: 70px;
        margin-top:10px!important;
        margin-bottom:10px!important;
        background: #e0e0e0;
        border:5px solid #e0e0e0;
    }
    .md-divider.md-inset {
        margin-left: 112px;
    }
</style>



