<template>
    
    <div id="banner">
       <swiper :options="swiperOption"  ref="mySwiper">
          
          <div class="swiper-slide" v-for="item in banner">            
            <img :src="'https://api.bdhrbdf.com'+item.field_image_m" alt="">
          </div>  
               
          <div class="swiper-pagination" slot="pagination"></div>     
      </swiper>
    </div>
     
</template>

<script>
  import axios from 'axios'
  import { swiper, swiperSlide } from 'vue-awesome-swiper'
  export default {
    name: 'Banner',
    components: {  
            swiper,  
            swiperSlide  
        }, 
    data () {
      return {      
        banner: [],
        swiperOption: {                  
                notNextTick: true,  
                pagination: '.swiper-pagination',  
                slidesPerView: 'auto',  
                centeredSlides: true,  
                paginationClickable: true,  
                spaceBetween: 30,  
                    onSlideChangeEnd: swiper => {  
                        //这个位置放swiper的回调方法  
                        this.page = swiper.realIndex+1;  
                        this.index = swiper.realIndex;  
                    }  
                }  
      }
    },
    
    methods: {
      fetchDate() {
        // 使用 axios获取数据          
        axios.get('https://api.bdhrbdf.com/api/jinan/banner')    
          .then(reponse=>{      
            this.banner=reponse.data;
          })
          .catch(error=>{
            alert('error');
          })
        },
        
      },

    created() {
      // 组件创建完后获取数据，
      // 此时 data 已经被 observed 了
      this.fetchDate();      
    },
    computed: {  
  
            swiper() {  
              return this.$refs.mySwiper.swiper;  
            }  
        },  
    mounted () {       
       this.swiper.slideTo(0, 0, false);
    },  
  
  }


  

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #banner img{
    max-width: 100%;
    height:auto;
  }
</style>


