<template>
  <div class="container doctor">
    <ul>
      <li v-for="item in data">          
        <p><img class="img-fluid" :src="item.field_image_m[0].url" alt=""></p>
        <h2>{{item.title[0].value}}</h2>
        <p>{{item.field_position[0].value}}</p>
        <p>{{item.field_company[0].value}}</p>
        <p>{{item.field_description[0].value}}</p>        
        <p>{{item.field_skill[0].value}}</p>
      </li>
    </ul>
    
  </div>    
</template>

<script>

import axios from 'axios';
export default {
  name: 'Doctor',  
  data () {
    return {      
      data:[]
    }  
  },
  methods: {
    fetchDate(){
      axios.get('https://api.bdhrbdf.com/api/node/'+this.$route.params.id)    
      .then(reponse=>{      
        this.data=reponse.data;
        console.log(reponse.data);
      })
      .catch(error=>{
        alert('error');
      })
    }
  },
  created() {
    // 组件创建完后获取数据，
    // 此时 data 已经被 observed 了
    this.fetchDate();
  },
  watch: {
    // 如果路由有变化，会再次执行该方法
    "$route": "fetchDate"
  }  
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>  
  .doctor h2{
    text-align: center;
  }
</style>


